<template>
    <div class="telo">
      <HeaderH title="Kontakt servis IBooking"/>
  
      <b-alert :variant="statusnaPorukaTip" :show="statusnaPoruka !== null">
        {{ statusnaPoruka }}
      </b-alert>
  
      <div>
        <b-container fluid>
          <b-row>
            <b-col sm="3">
              <label for="ime">Ime</label>
            </b-col>
            <b-col sm="9">
              <b-form-input id="ime" :state="validnoIme" v-model="forma.ime"></b-form-input>
            </b-col>
          </b-row>
          <b-row>
            <b-col sm="3">
              <label>Prezime</label>
            </b-col>
            <b-col sm="9">
              <b-form-input id="prezime" :state="validnoPrezime" v-model="forma.prezime"></b-form-input>
            </b-col>
          </b-row>
          <b-row>
            <b-col sm="3">
              <label>Email</label>
            </b-col>
            <b-col sm="9">
              <b-form-input id="email" :state="validnoEmail" v-model="forma.email"></b-form-input>
            </b-col>
          </b-row>
          <b-row>
            <b-col sm="3">
              <label>Vase pitanje upisite ovde:</label>
            </b-col>
            <b-col sm="9">
              <b-form-textarea id="textarea" :state="validnaPrijava" v-model="forma.prijava" :rows="4"></b-form-textarea>
            </b-col>
          </b-row>
        </b-container>
        <b-button @click="posalji()" variant="primary">Posalji</b-button>
      </div>
    </div>
  </template>
  
  <script>
  import HeaderH from '../components/HeaderH.vue';
  
  export default {
    name: 'App',
    components: {
      HeaderH
    },
    data() {
      return {
        headerTitle: "IBooking Kontakt/Info",
        statusnaPoruka: null,
        statusnaPorukaTip: null,
        forma: {
          ime: null,
          prezime: null,
          email: null, // Dodano polje za email
          prijava: null
        }
      }
    },
    computed: {
      validnoIme() {
        return this.forma.ime && this.forma.ime.length > 2;
      },
      validnoPrezime() {
        return this.forma.prezime && this.forma.prezime.length > 2;
      },
      validnoEmail() {
        return this.forma.email && this.forma.email.includes('@');
      },
      validnaPrijava() {
        return this.forma.prijava && this.forma.prijava.length > 2;
      }
    },
    methods: {
      posalji() {
        if (this.validnoIme && this.validnoPrezime && this.validnoEmail && this.validnaPrijava) {
          // Simulacija slanja podataka na server
          setTimeout(() => {
            this.statusnaPoruka = "Uspesno ste poslali vase pitanje, neko ce vam uskoro odgovoriti, vas IBooking hvala sto birate nas!";
            this.statusnaPorukaTip = 'success';
            // Resetovanje forme
            this.resetForme();
          }, 1000); // Umesto ovog setTimeouta, stavite vaš stvarni kod za slanje podataka na server
        } else {
          this.statusnaPoruka = "Molimo vas da ispravno popunite sva polja";
          this.statusnaPorukaTip = 'danger';
        }
      },
      resetForme() {
        this.forma.ime = '';
        this.forma.prezime = '';
        this.forma.email = '';
        this.forma.prijava = '';
      }
    }
  }
  </script>
  
  <style scoped>
  /* Vaš CSS ovdje */
  </style>
  