<template>
    <div class="glavni">
    <div class="destinacija">
        <h1 class="i">Korisne informacije o destinaciji</h1>
        <img :src="'/destinacije/' + destinacija.putanja" width="350" height="200" class="slika">
    </div>

    <div class="info">

        <h2>Drzava:</h2>
        <h3>{{destinacija.drzava}}</h3>

        <h2>Jezik:</h2>
        <h3>{{destinacija.jezik}}</h3>

        <h2>Valuta:</h2>
        <h3>{{destinacija.valuta}}</h3>

        <h2>Popularna mesta:</h2>
        <h3>{{destinacija.restorani}}</h3>
    </div>

</div>
  </template>
  
  <script>
  import HeaderH from '../components/HeaderH.vue';
  
  export default {
    name: 'Destinacija',
    components: {
      HeaderH
    },
    data(){
      return{
        destinacija: null
      }
    },
    mounted(){
      let destinacijaId = this.$route.params.id;
      fetch(`http://localhost:9000/destinacije/${destinacijaId}`)
      .then( res=>res.json() )
      .then( data=>{
        this.destinacija = data;
      });
    }
  }

  </script>
  
