<template>
    <div class="destinacija" @click="kliknutaDestinacija(destinacija.id)">
      <img :src="'/destinacije/' + destinacija.putanja" width="350" height="200">
      <h3>{{ destinacija.drzava }}</h3>
    </div>
  </template>
  
  <script>
  export default {
    name: 'SingleDestination',
    props: {
      destinacija: Object
    },
    methods:{
    kliknutaDestinacija(id){
        this.$router.push({ name: 'Destinacija', params: { id: id } });
      }
    }

  }
  </script>
  
  <style scoped>
  .destinacija {
    width: 33.33%; /* Postavi širinu na 33.33% kako bi tri destinacije bile u istom redu */
    box-sizing: border-box; /* Uključi border i padding u širinu elementa */
    padding: 10px; /* Dodaj malo razmaka između destinacija */
    text-align: center; /* Centriraj tekst */
  }
  </style>
  