import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    partneri: []
  },
  getters: {
  },
  mutations: {
    addPartneri(state, partneri){
      state.partneri = partneri;
    }

  },
  actions: {
    async fetchPartneri({commit}){
      fetch("http://localhost:9000/partneri/")
        .then(res => res.json())
        .then(data => commit('addPartneri',data));
    }
  },
  modules: {
  }
})
