<template>
    <div>
      <HeaderH title="Partneri sa kojima saradjuje IBooking"/>
  
      <b-table v-if="paginatedPartneri"
        striped hover
        :items="paginatedPartneri"
        :fields="fields"
        id="tabelaPartneri"
      >
        <template v-slot:cell(naziv)="data">
          <div class="partneri-item">
            <img :src="'/partneri/' + data.item.putanja" alt="Slika partnera" class="partneri-slika">
            <span class="partneriNaziv">{{ data.item.naziv }}</span>
          </div>
        </template>
      </b-table>
  
      <b-pagination
        v-model="currentPage"
        :total-rows="partneri.length"
        :per-page="5"
        @change="handlePageChange"
      />
    </div>
  </template>
  
  <script>
  import HeaderH from '../components/HeaderH.vue';
  import { mapActions, mapState } from 'vuex';
  
  export default {
    name: 'Partneri',
    components: {
      HeaderH
    },
    data() {
      return {
        
        paginatedPartneri: [],
        currentPage: 1,
        fields: [
          { key: 'naziv', label: 'Naziv' },
          { key: 'adresa', label: 'Adresa' }
        ]
      }
    },
    mounted() {
      fetch("http://localhost:9000/partneri/")
        .then(res => res.json())
        .then(data => {
          this.partneri = data;
          this.paginateData();
        });
    },
    computed:{
    ...mapState([
      'kompanije'
    ])
  },

    methods: {
      paginateData() {
        const start = (this.currentPage - 1) * 5;
        const end = start + 5;
        this.paginatedPartneri = this.partneri.slice(start, end);
      },
      handlePageChange(page) {
        this.currentPage = page;
        this.paginateData();
      }
    }
  }
  </script>
  
  <style scoped>
  .partneri-item {
    display: flex;
    align-items: center;
  }
  
  .partneri-slika {
    width: 200px; 
    height: 160px; 
    margin-left: 2%;
    margin-right: 10px; 
  }
  
  .partneriNaziv {
    margin-left: 19%;
  }
  </style>
  