import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
import Destinacija from '../views/Destinacija.vue'
import Partneri from '../views/Partneri.vue'
import Informacije from '../views/Informacije.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/destinacija/:id',
    name: 'Destinacija',
    component: Destinacija
  },
  {
    path: '/partneri',
    name: 'Partneri',
    component: Partneri
  },
  {
    path: '/pitanja-informacije',
    name: 'Informacije',
    component: Informacije
  }



]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
